footer {
 padding: 30px;
 margin: auto -15px 0;
 background: url('/assets/images/footer-pattern-bg.png');
 border-top: 1px dashed #dedede;

 > nav {
 padding: 0 25px 15px;
 margin: 0;
 text-align: center;

 a {
 margin: 2px 4px;
 display: block;
 color: #333333;
 text-align: center;
 font-size: 14px;
 font-weight: 500;
 letter-spacing: 0.8px;
 transition: all 0.3s ease-in-out;
 }
 }

 > p {
 text-align: center;
 color: #797878;
 font-size: 13px;
 line-height: 25px;
 padding: 0 15px;
 letter-spacing: 1px;
 margin: 0;
 }
}
