/* Aside styles */
@import "blocks/aside";

/* Scroll to top button */
@import "blocks/scroll-to-top";

#page {
 margin-top: 50px;
 margin-bottom: 50px;

 main {
 background: #ffffff;
 box-shadow: -2px -1px 88px 0px rgba(0,0,0,0.17);
 display: flex;
 flex-direction: column;

 #title {
 padding: 23px 15px 15px;

 span {
 margin: 0.83em 0;
 display: inline-block;
 font-size: 28px;
 letter-spacing: 0.5px;
 font-weight: 600;
 }
 }

 .content {
 padding-top: 30px;
 padding-bottom: 60px;
 border-top: 1px dashed #dedede;
 margin-right: -15px;
 margin-left: -15px;
 }

 p {
 font-size: 16px;
 line-height: 30px;
 color: #696868;
 font-weight: 300;
 letter-spacing: 0.8px;
 margin: 0 0 10px;
 }

 /* Footer styles */
 @import "blocks/footer";
 }
}
