.about-block {
 -ms-flex: 0 0 330px;
 flex: 0 0 330px;

 .fixed {
 box-shadow: -2px -1px 88px 0px rgba(0,0,0,0.17);
 }

 .avatar {
 img {
 width: 100%;
 }
 }

 .detail {
 text-align: center;
 margin-bottom: 30px;
 background: #ffffff;

 .me {
 padding: 23px 0;
 border-bottom: 1px dashed #cccccc;

 span {
 display: block;
 }

 .name {
 font-size: 21px;
 margin: 10px 3px;
 }

 .profile {
 color: #c1c1c1;
 font-weight: 400;
 line-height: 30px;
 font-size: 16px;
 }
 }

 .socials {
 list-style: none;
 padding: 20px;
 margin: 0;
 text-align: center;

 &:before,
 &:after {
 content: " ";
 display: table;
 }

 &:after {
 clear: both;
 }

 li {
 display: inline-block;
 margin: 2px 4px;
 a {
 display: block;
 width: 40px;
 height: 40px;
 line-height: 42px;
 color: #fff;
 text-align: center;
 border: 0 none;
 border-radius: 50%;
 font-size: 17px;
 box-shadow: 0 1px 2px 0 rgba(90, 91, 95, 0.15);
 transition: all 0.3s ease-in-out;

 &:hover {
 box-shadow: 0 8px 15px 0 rgba(90, 91, 95, 0.33);
 }

 &.facebook {
 background-color: #3b5998;
 }

 &.twitter {
 background-color: #1da1f2;
 }

 &.instagram {
 background-color: #464545;
 }

 &.linkedin {
 background-color: #0077b5;
 }

 &.github {
 background-color: #333333;
 }

 &.skype {
 background-color: #00aff0;
 }
 }
 }
 }
 }
}

