/* Grid system */
@import "_imports/plugins/bootstrap-grid";
@import "_imports/plugins/fontawesome";

/* Global Style */

html {
 position: relative;
 min-height: 100%;

 body {
 font-family: "Raleway", sans-serif;
 font-weight: 400;
 color: #333;
 margin: 0;
 padding: 0;
 }

 a {
 text-decoration: none !important;
 color: #1abc9c;

 &:hover,
 &:focus,
 &:active {
 text-decoration: none;
 outline: none;
 color: #333333;
 }
 }

 .button {
 border: none;
 font-family: inherit;
 font-size: 16px;
 color: inherit;
 background: none;
 cursor: pointer;
 padding: 10px 60px 10px 0;
 display: inline-block;
 margin: 8px 30px 8px 5px;
 letter-spacing: 1px;
 font-weight: 600;
 outline: none;
 position: relative;
 -webkit-transition: all 0.3s;
 -moz-transition: all 0.3s;
 transition: all 0.3s;

 &:after {
 content: '';
 position: absolute;
 z-index: -1;
 -webkit-transition: all 0.3s;
 -moz-transition: all 0.3s;
 transition: all 0.3s;
 }

 &:before {
 font-family: 'FontAwesome';
 speak: none;
 font-style: normal;
 font-weight: normal;
 font-variant: normal;
 text-transform: none;
 line-height: 1;
 position: relative;
 -webkit-font-smoothing: antialiased;
 }
 }

 .button-style {
 color: #1abc9c;
 overflow: hidden;

 &:before {
 position: absolute;
 font-size: 27px;
 line-height: 38px;
 color: #1abc9c;
 -webkit-transition: all 0.3s;
 -moz-transition: all 0.3s;
 transition: all 0.3s;
 }

 &:active:before {
 color: #1abc9c;
 }
 }

 .button-anim {
 &:before {
 left: 60%;
 opacity: 0;
 top: 0;
 }

 &:hover:before {
 left: 75%;
 opacity: 1;
 }
 }
}

/* Page styles */
@import "_imports/page";

/* Media queries */
@import "_imports/media-queries";
