/* blocks/aside.scss */
.about-block {
 @media all and (max-width: 1199px) {
 -ms-flex: 0 0 270px;
 flex: 0 0 270px;
 }

 @media all and (max-width: 991px) {
 flex: 0 0 100%;
 margin-bottom: 30px;

 > div {
 display: flex;
 max-height: 150px;

 .avatar {
 width: 15%;
 min-width: 100px;

 img {
 width: 100%;
 height: 100%;
 object-fit: cover;
 }
 }

 .detail {
 flex: 1;
 margin-bottom: 0;

 .me {
 padding: 10px 20px 0;

 .name {
 margin: 0 3px 5px;
 }

 .profile {
 margin-bottom: 5px;
 }
 }

 .socials {
 padding: 15px 20px;
 }
 }
 }
 }

 @media all and (max-width: 575px) {
 > div .avatar {
 width: 20%;
 }
 }
}

